import { ref } from 'vue'
// import { throwError } from '../error-manager'
import { LOCAL_STORAGE_MAP } from './consts'

function createLocalStorageManager() {
  const reactiveStorageMap = ref({} as LOCAL_STORAGE_MAP)

  function get<T extends keyof LOCAL_STORAGE_MAP>(key: T): LOCAL_STORAGE_MAP[T] | null {
    const value = reactiveStorageMap.value[key]
    if (!value) {
      const v = localStorage.getItem(String(key))
      try {
        if (v) {
          const value2 = JSON.parse(decodeURIComponent(atob(v)))
          reactiveStorageMap.value[key] = value2
          return value2
        }
      } catch (e) {
        // throwError({ err: '解key失败', data: key })
      }
      return null
    }
    return value
  }

  function set<T extends keyof LOCAL_STORAGE_MAP>(key: T, value: LOCAL_STORAGE_MAP[T]) {
    try {
      const v = btoa(encodeURIComponent(JSON.stringify(value)))
      localStorage.setItem(String(key), v)
      reactiveStorageMap.value[key] = value
    } catch (e) {
      // throwError({ err: 'set key-value失败', data: { key, value } })
    }
  }
  return function manager() {
    return {
      get,
      set,
    }
  }
}

function createSessionStorageManager() {
  const reactiveStorageMap = ref({} as LOCAL_STORAGE_MAP)

  function get<T extends keyof LOCAL_STORAGE_MAP>(key: T): LOCAL_STORAGE_MAP[T] | null {
    const value = reactiveStorageMap.value[key]
    if (!value) {
      const v = sessionStorage.getItem(String(key))
      try {
        if (v) {
          const value2 = JSON.parse(decodeURIComponent(atob(v)))
          reactiveStorageMap.value[key] = value2
          return value2
        }
      } catch (e) {
        // throwError({ err: '解key失败', data: key })
      }
      return null
    }
    return value
  }

  function set<T extends keyof LOCAL_STORAGE_MAP>(key: T, value: LOCAL_STORAGE_MAP[T]) {
    try {
      const v = btoa(encodeURIComponent(JSON.stringify(value)))
      sessionStorage.setItem(String(key), v)
      reactiveStorageMap.value[key] = value
    } catch (e) {
      // throwError({ err: 'set key-value失败', data: { key, value } })
    }
  }
  return function manager() {
    return {
      get,
      set,
    }
  }
}

export const useLocalStorage = createLocalStorageManager()

export const useSessionStorage = createSessionStorageManager()
