import { ref, onMounted, onUnmounted, Ref, getCurrentInstance } from 'vue'

const viewMap: Ref<Map<string, any>> = ref(new Map<string, any>())

export function registerView(key: string, value: any) {
  if (!(window as any).viewMap) {
    ;(window as any).viewMap = viewMap.value
  }
  onMounted(() => {
    viewMap.value.set(key, value)
  })

  onUnmounted(() => {
    viewMap.value.delete(key)
  })
}

export function getViewMap() {
  return viewMap
}

