type RacerOptions = {
  onlyFirst?: boolean // 是否只执行第一个，后续的返回第一个的执行结果
  onlyOnce?: boolean // 是否竞态一次后，后续的不再执行并且抛错
}

export function createRacer<T>(options?: RacerOptions) {
  type Res = T
  type Resolver = (value: Res | PromiseLike<Res>) => void
  type Rejecter = (reason?: string) => void
  type PromiseFunc = (...args: any) => Promise<Res>

  const defaultOpts: RacerOptions = {
    onlyFirst: false,
    onlyOnce: false,
  }

  const opts = Object.assign({}, defaultOpts, options || {})

  if (opts.onlyFirst && opts.onlyOnce) {
    console.error({ err: 'utils:onlyFirst和onlyOnce不能同时为true' })
    // throwError({ err: 'utils:onlyFirst和onlyOnce不能同时为true' })
  }

  const _promises: PromiseFunc[] = []
  const _resolvers: Map<
    number,
    {
      resolve: Resolver
      reject: Rejecter
    }
  > = new Map()

  let id = 0

  let hasUseFirst = false
  let hasUseOnce = false

  let firstPromises: Promise<Res>
  let onceSuccessId: number

  return {
    async race(promise: PromiseFunc[]): Promise<Res> {
      if (opts.onlyFirst) {
        return this.raceFirst(promise)
      }
      if (opts.onlyOnce) {
        return this.raceOnce(promise)
      }
      return this.raceNormal(promise)
    },

    async raceNormal(promise: PromiseFunc[]): Promise<Res> {
      _promises.push(...promise)
      const t_promise = new Promise<Res>((resolve, reject) => {
        id += 1
        _resolvers.set(id, { resolve, reject })
        this.raceFunc(id)
      })
      return t_promise
    },

    async raceFunc(id: number) {
      const __promises = _promises.map((p) => p())
      const res = await Promise.race(__promises).catch((e) => console.error(e))
      if (typeof res !== 'undefined') {
        _promises.splice(0, _promises.length)
        const keys = Array.from(_resolvers.keys())
        keys.forEach((key) => {
          const resolver = _resolvers.get(key)
          if (resolver) {
            if (key === id) {
              resolver.resolve(res)
            } else {
              resolver.reject(`utils:racer竞争失败, success id: ${id}, fail id: ${key}`)
            }
          }
        })
      }
    },

    async raceFirst(promise: PromiseFunc[]): Promise<Res> {
      if (!hasUseFirst) {
        _promises.push(...promise)
        const t_promise = new Promise<Res>((resolve, reject) => {
          id += 1

          _resolvers.set(id, { resolve, reject })
          this.raceFunc2(id)
        })
        firstPromises = t_promise
        hasUseFirst = true
        return t_promise
      } else {
        return firstPromises
      }
    },

    async raceFunc2(id: number) {
      const __promises = _promises.map((p) => p())
      const res = await Promise.race(__promises).catch((e) => console.error(e))
      // console.warn('h5_pay_page_show', 999, res)
      if (typeof res !== 'undefined') {
        _promises.splice(0, _promises.length)
        const keys = Array.from(_resolvers.keys())
        keys.forEach((key) => {
          const resolver = _resolvers.get(key)
          if (resolver) {
            if (key === id) {
              // console.warn('h5_pay_page_show', 777)
              resolver.resolve(res)
            } else {
              console.warn('h5_pay_page_show', 888)
              resolver.reject(`utils:racer竞争失败, success id: ${id}, fail id: ${key}`)
            }
          }
        })
      } else {
        _promises.splice(0, _promises.length)
        // 报错重置为false
        hasUseFirst = false
      }
    },

    async raceOnce(promise: PromiseFunc[]): Promise<Res> {
      _promises.push(...promise)
      const t_promise = new Promise<Res>((resolve, reject) => {
        id += 1
        _resolvers.set(id, { resolve, reject })
        this.raceFunc3(id)
      })
      return t_promise
    },

    async raceFunc3(id: number) {
      const __promises = _promises.map((p) => p())
      const res = await Promise.race(__promises).catch((e) => console.error(e))
      if (typeof res !== 'undefined') {
        _promises.splice(0, _promises.length)
        if (!hasUseOnce) {
          hasUseOnce = true
          const keys = Array.from(_resolvers.keys())
          keys.forEach((key) => {
            const resolver = _resolvers.get(key)
            if (resolver) {
              if (key === id) {
                onceSuccessId = id
                resolver.resolve(res)
              } else {
                resolver.reject(`utils:racer once竞争失败, success id: ${id}, fail id: ${key}`)
              }
            }
          })
        } else {
          const keys = Array.from(_resolvers.keys())
          keys.forEach((key) => {
            const resolver = _resolvers.get(key)
            if (resolver) {
              resolver.reject(
                `utils:racer 已经有成功过的了, success id: ${onceSuccessId}, fail id: ${key}`
              )
            }
          })
        }
      }
    },
  }
}

export const SENSOR_SERVER_URL = ref('https://sawa-sa-proxy.badambiz.com:10443/sa')

function initDomainDial() {
  const racer = createRacer<boolean>({ onlyFirst: true })

  async function init(): Promise<boolean> {
    const r = await getConfigAndDial()
    o.isInit = r
    return r
  }
  const o = {
    isInit: false,
    async exec() {
      return racer.race([init])
    },
  }
  return o
}

