export const BASE_PUBLIC_PATH =
  process.env.VUE_APP_ENV === 'development' && process.env.VUE_APP_FLAVOR === 'ksa'
    ? 'https://test-isawa.badambiz.com/html/sawa-offical-website/ksa/'
    : location.origin + '/'

export async function initSensors() {
  const racer = createRacer<boolean>({ onlyFirst: true })

  async function initWebSensorsSdk(batch = false): Promise<boolean> {
    console.log('zeze')
    await initDomainDial().exec()
    const ls: any = useLocalStorage()
    const u = ls.get('sawa_ow_last_use_sensors_base')
    if (u) SENSOR_SERVER_URL.value = `https://${u}/sa`
    const send_type = 'ajax'
    const log = logger.getEnable()
    const url = new URL('./sensorsdata.min.js', BASE_PUBLIC_PATH).href
    console.log('initWebSensorsSdk', SENSOR_SERVER_URL.value, url, process.env)

    const _o = {
      datasend_timeout: 6000, // 一次请求超过多少毫秒的话自动取消，防止请求无响应。
      send_interval: 2000, // 间隔多少毫秒发一次数据。
      storage_length: 200, // 存储 localStorage 条数最大值，默认：200 。
    }
    const batch_send = batch ? _o : false
    const app_js_bridge = false

    let sdk: any = 'sawa_saudi'

    await initWebSensors(sdk, log, send_type, SENSOR_SERVER_URL.value, url, {
      app_js_bridge,
      batch_send,
      max_string_length: 1200,
    })

    o.isInit = true

    if (window.sensors) {
      window.sensors.on('dataSendSuccess', (obj: any) => {
        const trace = (window as any).firebasePerformanceTrace
        if (trace) {
          const t_firebase = trace('sensors_send_success')
          t_firebase.start()
          t_firebase.putAttribute('result', '1')
          t_firebase.stop()
        }
        handleObj(obj)
      })
      window.sensors.on('dataSendFail', (obj: any) => {
        const trace = (window as any).firebasePerformanceTrace
        if (trace) {
          const t_firebase = trace('sensors_send_success')
          t_firebase.start()
          t_firebase.putAttribute('result', '0')
          t_firebase.stop()
        }
        handleObj(obj, true)
      })
      return true
    } else {
      return false
    }
  }

  function handleObj(obj: any, fail = false) {
    const ts = getDayJs(getSysTs()).valueOf()
    const day = Number(getDayJs(ts).format('YYYYMMDD'))
    const datas: any = obj?.data
    let e_us = []
    let d = {}
    try {
      e_us = datas
        .filter((i: any) => !!i?.properties?.s_event_uuid)
        .map((i: any) => i?.properties?.s_event_uuid)
    } catch (e) {
      e_us = [datas?.properties?.s_event_uuid].filter((i) => !!i)
      d = datas
    }
    console.log(fail ? 'data send fail' : 'data send success', obj, e_us)
    if ((e_us && e_us.length > 0) || fail) {
    }
  }

  const o = {
    isInit: false,
    async exec() {
      return racer.race([initWebSensorsSdk])
    },
  }

  return initWebSensorsSdk()
}
