function initRemFontSize() {
  const maxWidth = window.innerWidth < 800 ? window.innerWidth : 800
  const pixel = maxWidth / 50
  document.documentElement.style.fontSize = `${pixel}px`
}

initRemFontSize()

async function report() {
  await initSensors()

  const sourceMap: any = {
    0: 'email',
    1: '短信',
    2: 'whatsapp',
  }
}

async function setTime() {
  const time = await getServTs(appType.sawa)
  updateTsDelta(time)
}

function init() {
  return Promise.all([initConfig(), report()])
}

exec(async () => {
  // calculateWindow(750)

  try {
    await ready()
    init()
    DayJs.setOffset(TimeType.Ksa)
    await addWebpAvifSupport({
      avif: getPlatform().isIPhone,
    })
    // initErrorReporter(name, nanoid)
    reportSensors('act_meaningful_content_loaded', true)
    metric.observeSourceMetric([
      {
        desc: 'banner',
        source_name: 'banner',
      },
    ])
  } catch (err) {
    console.error(err)
  }

  new Vue({
    render: (h) => h(Main),
    store,
  }).$mount('#app')
})
