export function getMp4url(url: string) {
  const DOMAIN = window.location.hostname
  if (DOMAIN.indexOf('cdn-') === -1) {
    return url.replace('cdn-', '')
  }
  return url
}

export function preloadVideo(source: string) {
  const URL = window.webkitURL || window.URL

  return new Promise((resolve, reject) => {
    const xhr = new XMLHttpRequest()
    xhr.open('GET', source, true)
    xhr.responseType = 'blob'

    xhr.onload = function () {
      if (xhr.status === 200 || xhr.status === 304) {
        const res = xhr.response
        if (/iphone|ipad|ipod/i.test(navigator.userAgent)) {
          const fileReader = new FileReader()
          fileReader.onloadend = function () {
            const resultStr = fileReader.result! as string
            resolve(resultStr)
          }
          fileReader.readAsDataURL(xhr.response)
        } else {
          resolve(URL.createObjectURL(res))
        }
      } else {
        reject(new Error(`http response invalid${xhr.status}`))
      }
    }

    xhr.send()
  })
}

