/* eslint-disable camelcase */
import { api, getSessionKey, getArgsFromUrl } from '@badam/common-utils'
import axios from 'axios'

api.getAxiosInstance().defaults.baseURL =
  process.env.VUE_APP_ENV === 'development'
    ? 'https://zvod-release.badambiz.com:40042'
    : process.env.VUE_APP_BASE_URL

const publicUrl =
  process.env.VUE_APP_ENV === 'development'
    ? `${'https://zvod-release.badambiz.com:40042'}/api/thirdparty/sawa_warm_up_202402_api`
    : `${process.env.VUE_APP_BASE_URL}/third/sawa_warm_up_202402_api`

api.setWgetEnable(true)

export const session_key = getSessionKey() || 'testskey_2000026159'

const url =
  process.env.VUE_APP_ENV === 'development'
    ? 'https://test12-pk.badambiz.com'
    : 'https://arab-pk.badambiz.com'

interface MyInfoRes {
  uid: number
  nickname: string
  icon: string
}

export function getMyUserInfo() {
  return api.wget<MyInfoRes>(`${url}/api/user/get_my_info/`, {
    session_key,
  })
}

export function info() {
  return api.wget<any>(`${publicUrl}/info`, {
    session_key,
  })
}

export function access() {
  return api.wget<any>(`${publicUrl}/access`, {
    session_key,
  })
}

interface SubscribeStatus {
  isSubscribed: boolean
}

export function subscribeStatus() {
  return api.wget<SubscribeStatus>(`${publicUrl}/subscribe_status`, {
    session_key,
  })
}

export function subscribeAct() {
  return api.wget<any>(`${publicUrl}/subscribe_act`, {
    session_key,
  })
}

export function getPkgInfo() {
  return api.wget<MyInfoRes>(`${url}/api/sys/config/`, {
    channel: 'app_pkg_info',
    pure: 1,
  })
}

export function backupDomains() {
  return api.wget('/api/sys/backup_domains/', {})
}
export function getTestUrl() {
  if (
    window.location.href.indexOf('test-isawa') >= 0 ||
    window.location.href.indexOf('test.github') >= 0
  ) {
    return (process.env as any).VUE_APP_PROXY_URL
  }
  // return (process.env as any).VUE_APP_BASE_URL
  return '/'
}
const isDebug = getArgsFromUrl('is_debug') || process.env.VUE_APP_ENV === 'development'

let sysInfoBaseUrl =
  getArgsFromUrl('is_debug') || process.env.VUE_APP_ENV === 'development'
    ? getTestUrl()
    : process.env.VUE_APP_BASE_URL

export function setApiBaseUrl(url_: string) {
  if (url_) {
    axios.defaults.baseURL =
      isDebug || process.env.VUE_APP_ENV === 'development' ? getTestUrl() : url_
    api.getAxiosInstance().defaults.baseURL = axios.defaults.baseURL
    sysInfoBaseUrl =
      getArgsFromUrl('is_debug') || process.env.VUE_APP_ENV === 'development' ? getTestUrl() : url_
  }
}
