import { backupDomains, setApiBaseUrl } from '@/api/index'
import { handleApiResult, api } from '@badam/common-utils'
import FirebaseManager from '../firebase-manager'
import { useLocalStorage } from '../local-storage-manager'
import { LOCAL_STORAGE_KEY } from '../local-storage-manager/consts'
// import reporter from '../report-manager'
// import { EventName } from '../report-manager/events'

const LOG_TAG = 'domain-dial'

export async function waitValue<T>(ts: number, value: T): Promise<T> {
  return new Promise((r) => {
    setTimeout(() => {
      r(value)
    }, ts)
  })
}

export async function raceWithTime<T>(promise: Promise<T>, time = 5000) {
  return Promise.race([waitValue(time, null), promise])
}

export enum DialType {
  神策 = 1,
  API = 2,
}

export async function getFirebaseConfig(): Promise<string | null> {
  return raceWithTime(FirebaseManager.getInstance().getValueFromConfig('ow_conf'))
}

export async function getBackupDomains() {
  const res = await backupDomains()
  if (res[0]) {
    const [, { data }] = res
    const d = JSON.parse(data as any)
    console.log(LOG_TAG, 'data', d)
  }
}

const dial_timeout = 5000

export async function dailUrls(urls: string[], type: DialType): Promise<string> {
  const promise: Promise<string> = new Promise((r) => {
    for (let i = 0; i < urls.length; i += 1) {
      const url = urls[i]
      if (type === DialType.API) {
        dialGoPk(url).then((res) => {
          if (res) {
            r(url)
          }
        })
      } else if (type === DialType.神策) {
        dialSensors(url).then((res) => {
          if (res) {
            r(url)
          }
        })
      }
    }
  })
  const timeout: Promise<string> = new Promise((r) =>
    setTimeout(() => {
      r('')
    }, dial_timeout)
  )
  return Promise.race([promise, timeout])
}

export async function dialGoPk(url: string): Promise<string> {
  const res = await api.get(`https://${url}/api/sys/h5_info`, {})
  return res[0] ? url : ''
}

export async function dialSensors(url: string): Promise<string> {
  const res = await api.get(`https://${url}/api/gm/hii`, {})
  return res[0] ? url : ''
}

export async function dial(urls: string[], type: DialType) {
  const ls = useLocalStorage()
  if (type === DialType.API) {
    const res = await dailUrls(urls, DialType.API)
    if (res) {
      ls.set(LOCAL_STORAGE_KEY.上次使用过的API_BASE, res)
      setApiBaseUrl(`https://${res}/`)
    } else {
      console.error('dial api timeout')
      // reporter.report(
      //   EventName.h5_init_error,
      //   { error: 'dial api timeout' },
      //   { onlyFirebase: true }
      // )
    }
  } else if (type === DialType.神策) {
    const res = await dailUrls(urls, DialType.神策)
    if (res) {
      console.log('神策res', res, LOCAL_STORAGE_KEY.上次使用过的神策域名)
      ls.set(LOCAL_STORAGE_KEY.上次使用过的神策域名, res)
    } else {
      console.error('dial api timeout')
      // reporter.report(
      //   EventName.h5_init_error,
      //   { error: 'dial sensors timeout' },
      //   { onlyFirebase: true }
      // )
    }
  }
}

export function dial2(go_pk: string[] | null, se: string[] | null) {
  const ls = []
  if (go_pk) {
    ls.push(dial(go_pk, DialType.API))
  }
  if (se) {
    ls.push(dial(se, DialType.神策))
  }
  return Promise.all(ls)
}

export async function getConfigAndDial(): Promise<boolean> {
  const ls: any = useLocalStorage()
  FirebaseManager.getInstance().init()
  const conf = await getFirebaseConfig()

  console.log('conf', conf)

  if (conf) {
    const o = JSON.parse(conf)
    const go_pk: string[] = o.go_pk
    const se: string[] = o.se
    console.log(LOG_TAG, 'conf', go_pk, se)
    ls.set(LOCAL_STORAGE_KEY.备用apilist, go_pk)
    ls.set(LOCAL_STORAGE_KEY.备用神策list, se)
    await dial2(go_pk, se)
    return true
  } else {
    const go_pk = ls.get(LOCAL_STORAGE_KEY.备用apilist)
    const se = ls.get(LOCAL_STORAGE_KEY.备用神策list)
    await dial2(go_pk, se)
    return true
  }
}
