/* eslint-disable */
import { getCurrentInstance, ref, onMounted, onUnmounted } from 'vue'
import { getArgsFromUrl } from '@badam/common-utils'
import { report } from './report'
import { registerView } from './view-manager'

export type Comp = CompData & CompProps & CompExpose

/**
 * 定义组件data数据类型
 */
export type CompData = {
  a: number
  b: number
  report: typeof report
}

/**
 * 定义组件props数据类型
 */
export type CompProps = {
  compId?: string // 必需有的值，如果是列表item，则传入key值即可
}

/**
 * 定义组件expose数据类型
 */
export type CompExpose = {
  sayHello: typeof sayHello
}

/**
 * @description: 逻辑入口函数
 */
export function useComp() {
  registerView('home', getCurrentInstance())

  onMounted(() => {
    console.log('test onMounted')
    if (getArgsFromUrl('tab') === '1') {
      if (getArgsFromUrl('jump_app_store')) {
        if (window.JavaBridgeObj) {
          JavaBridgeObj?.router(
            'https://apps.apple.com/us/app/sawa-ksa/id6443823893?isOutBrowser=true'
          )
        } else {
          location.href = 'https://apps.apple.com/us/app/sawa-ksa/id6443823893?isOutBrowser=true'
        }
      }
      onChange(Tab.ludo)
    }
    if (getArgsFromUrl('tab') === '2') {
      onChange(Tab.sawa)
    }
  })
}

export function sayHello() {
  console.log('say hello')
}

export const expose: CompExpose = {
  sayHello,
}

export enum Tab {
  sawa = 1,
  ludo = 2,
}

export const tabs = [
  { tab: 'Sawa', index: Tab.sawa },
  { tab: 'Sawa Ludo', index: Tab.ludo },
]

export const curTab = ref(1)

export function onChange(i: Tab) {
  curTab.value = i
}
